.control {
  position: relative;
  display: block;
  padding: 10px 10px 0 54px;
  margin-right: 15px;
}
.control-mark {
  position: absolute;
  top: 2px;
  left: -7px;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 50%;
  margin-bottom: 16px;
}
.control-mark:last-child {
  margin-bottom: 0;
}
.control-input[type="radio"]:checked + .control-mark::before {
  position: absolute;
  top: 17px;
  left: 17px;
  width: 10px;
  height: 10px;
  background-color: #333333;
  border-radius: 50%;
  content: "";
  background-image: url("../img/form-tick.svg");
  background-repeat: no-repeat;
}
.control-input:hover + .control-mark {
  background-color: #e5e5e5;
}
.control-input:active + .control-mark {
  background-color: #d5d5d5;
}
