/*Просто пустая кликабельная кнопка*/
.main-nav__toggle {
  position: absolute;
  margin-left: auto;
  box-sizing: border-box;
  border: none;
  padding: 0;
  top: 17px;
  right: 6px;
  z-index: 2;
  display: block;
  width: 22px;
  height: 22px;
  background-color: transparent;
  cursor: pointer;
}
.main-nav__toggle:hover,
.main-nav__toggle:focus {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
/* Бургерное меню - верхняя палочка
остальные две палочки внизу - тенями**/
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: -2px;
  width: 22px;
  height: 4px;
  background-color: #81b3d2;
  box-shadow: 0 7px 0 0 #81b3d2, 0 14px 0 0 #81b3d2;
}
/*Состояние :active*/
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(129, 179, 210, 0.3);
  box-shadow: 0 7px 0 0 rgba(129, 179, 210, 0.3), 0 14px 0 0 rgba(129, 179, 210, 0.3);
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
  .main-nav--closed .main-nav__item--active {
    color: #ffffff;
    background-color: #81b3d2;
    height: 55px;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__item {
    display: block;
  }
}
/*КНОПКА*/
.main-nav--opened .main-nav__toggle {
  top: 17px;
  right: 19px;
  /*Крестик - двумя палочками*/
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 8px;
  right: -10px;
  width: 36px;
  height: 4px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-nav--opened .main-header__logo {
  display: none;
  z-index: -1;
}
.main-nav--closed .main-header__logo {
  z-index: 3;
}
